import './App.css';
import React, { useState } from "react";
import axios from "axios";

function App() {
  const [text, setText] = useState('');
  
  const onClick = async () => {
    return new Promise((resolve, reject) => {
      axios.get(`http://localhost:30500/api`)
      .then((res) => {
        console.log(res);
        resolve(res.data);
      })
      .catch((err) => {
        console.log(err);
        reject();
      })
    })
  }

  return (
    <div className="App">
      <button onClick={async () => {
        setText(await onClick());
      }}>Click!</button>
      <p>{text}</p>
    </div>
  );
}

export default App;
